
<template>
  <div class="padding-internal-card">
    <mu-tooltip
      content="Si necesitas un credito menor, haga doble click en Monto de Crédito  y escribalo"
    >
      <mu-text-field
        @click="validate"
        v-model="__montoDeCredito"
        :label="label"
        prefix="$"
        label-float
        action-icon="info"
        :action-click="()=>{showInfo = !showInfo}"
      ></mu-text-field>
      <mu-dialog title="Valor del Inmueble" width="360" :open.sync="showInfo">
        Si necesitas un credito menor,
        <b
          class="cursor-pointer"
          @click="()=>{showInfo = !showInfo;validate()}"
        >haga doble click en Monto de Crédito</b> y escribalo
        <mu-button slot="actions" flat color="primary" @click="()=>{showInfo = !showInfo}">Aceptar</mu-button>
      </mu-dialog>
      <!-- <a href="https://api.whatsapp.com/send?phone=5650149322&text=123">hola</a> -->
    </mu-tooltip>
  </div>
</template>
<script>
import { mapState, mapMutations, mapGetters } from "vuex";
import numeral from "numeral";

export default {
  data() {
    return {
      showInfo: false
    };
  },
  computed: {
    ...mapState(["montoDeCredito"]),
    ...mapGetters(["_montoDeCredito", "montoDeCreditoConEscrituras"]),
    __montoDeCredito: {
      get() {
        return numeral(this._montoDeCredito).format("0,0");
      },
      set(value) {
        let valorNumerico = numeral(value).value();
        this.updateMontoDeCredito(valorNumerico);
      }
    },
    label() {
      if (this.montoDeCreditoConEscrituras) {
        return "Monto De Crédito **Incluye escrituras";
      } else {
        return "Monto De Crédito";
      }
    }
  },
  methods: {
    ...mapMutations(["updateMontoDeCredito"]),
    validate() {
      // if (!this.montoDeCredito) {
      //   this.updateMontoDeCredito("0");
      // }
    }
  }
};
</script>
