
<template>
  <div  class="padding-internal-card" v-if="tipoDeApoyoSeleccionado == 'cofinavit'">
    <mu-text-field
      v-model="_montoDeApoyo"
      label="Con cuanto cuentas en cofinavit"
      prefix="$"
      label-float
    ></mu-text-field>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";

import numeral from "numeral";

export default {
  computed: {
    ...mapState(["montoDeApoyo", "tipoDeApoyoSeleccionado"]),
    _montoDeApoyo: {
      get() {
        return numeral(this.montoDeApoyo).format("0,0");
      },
      set(value) {
        let valorNumerico = numeral(value).value();
        this.updateMontoDeApoyo(valorNumerico);
      }
    }
  },
  methods: {
    ...mapMutations(["updateMontoDeApoyo"])
  }
};
</script>
