
<template>
  <div class="padding-internal-card">
    <mu-text-field
      v-model="_gastosMensuales"
      label="Gastos mensuales de tarjetas, autos, etc"
      prefix="$"
      label-float
    ></mu-text-field>
    <!-- <a href="https://api .whatsapp.com/send?phone=5650149322&text=123">hola</a> -->
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
import numeral from "numeral";

export default {
  computed: {
    ...mapState(["gastosMensuales"]),
    _gastosMensuales: {
      get() {
        return numeral(this.gastosMensuales).format("0,0");
      },
      set(value) {
        let valorNumerico = numeral(value).value();
        this.updateGastosMensuales(valorNumerico);
      }
    }
  },
  methods: {
    ...mapMutations(["updateGastosMensuales"])
  }
};
</script>
