<template>
  <div v-if="modal == 2" class="modal-cover">
    <div class="modal-content">
      <div class="card modal-card white inline-block">
        <!-- <h2 class="blue-text">Firma tu escritura en 3 o 4 semanas</h2> -->
        <img :src="require('@/assets/img/icons/share.svg')" class="modal-icon" />
        <div class="tabs">
          <div class="tab selected" style="padding-top:5px !important">
            <h3 style="color:#000 !important">COMPARTIR</h3>
          </div>
        </div>

        <mu-text-field v-model="_name" label="Nombre" label-float></mu-text-field>
        <mu-text-field v-model="_phone" :max-length="10" type="tel" label="Celular" label-float></mu-text-field>
        <mu-text-field v-model="_email" type="email" label="Correo Electrónico" label-float></mu-text-field>
        <mu-text-field v-model="message" label="Mensaje" label-float multi-line :rows="4"></mu-text-field>
        <br />
        <br />
        <div v-if="disabled" class="button-large" :class="{'disabled':disabled}">Correo Electrónico</div>
        <div
          v-else
          @click="toAddCredit"
          class="button-large"
          :class="{'disabled':disabled}"
        >Correo Electrónico</div>
        <div v-if="disabled" class="button-large" :class="{'disabled':disabled}">WhatsApp</div>
        <a
          :href="`https://api.whatsapp.com/send?phone=${contactData.phone}&text=${contactData.message}`"
          target="_blank"
          v-else
        >
          <div class="button-large" :class="{'disabled':disabled}">WhatsApp</div>
        </a>
        <div @click="updateModal(0)" class="button-large flat">Volver</div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations, mapActions } from "vuex";
import numeral from "numeral";
export default {
  data() {
    return {
      message: ""
    };
  },
  computed: {
    ...mapState([
      "modal",
      "activeBank",
      "name",
      "phone",
      "email",
      "tipoDeIngresoSeleccionado",
      "tipoDeApoyoSeleccionado",
      "montoDeApoyo",
      "ingresoMensual",
      "valorDelInmueble",
      "tipoDePago",
      "plazoSeleccionado",
      "montoDeCredito"
    ]),
    disabled() {
      if (this._name && this._phone && this._email && this.message) {
        return false;
      } else {
        return true;
      }
    },
    contactData() {
      let name = this.activeBank.bank.toUpperCase();
      let monthlyPayment = numeral(this.activeBank.monthlyPayment).format(
        "0,0"
      );
      let earnings = numeral(this.activeBank.earnings).format("0,0");
      let mortgageRate = (this.activeBank.mortgageRate * 100).toFixed(2);
      let requestedCredit = numeral(this.activeBank.requestedCredit).format(
        "0,0"
      );
      let preAuthorizedCredit = numeral(
        this.activeBank.preAuthorizedCredit
      ).format("0,0");

      let propertyCost = numeral(this.activeBank.propertyCost).format("0,0");

      return {
        phone: "52" + this._phone,
        message: ` ¡Hola ${this._name}! %0A%0A${this.message} %0A%0ATe sugerimos este crédito: %0A%0A*BANAMEX*%0A%0APago Mensual: *$ 69,856*%0AIngresos: *$ 139,712*%0ATasa Fija Anual: *9.65 %25*%0AValor del Inmueble: *10,000,000*%0ACrédito Solicitado: *6,800,000*%0ACrédito Pre Autorizado: *25,000,000*%0A %0A*¿Qué sigue?*%0A%0APor favor revisa el siguiente comparativo: %0A%0A*${this.buildURL}* %0A%0AEn breve nos comunicaremos, para ofrecerte mayor información.%0A%0AEn crédito hipotecario express, te asesoramos y llevamos de la mano hasta la firma de escritura, ¡SIN COSTO! %0A%0ATramítalo en:%0A%0Awww.creditohipotecarioexpress.com `
      };
    },
    _name: {
      get() {
        return this.name;
      },
      set(value) {
        this.updateName(value);
      }
    },
    _email: {
      get() {
        return this.email;
      },
      set(value) {
        this.updateEmail(value);
      }
    },
    _phone: {
      get() {
        return this.phone;
      },
      set(value) {
        this.updatePhone(value);
      }
    },
    buildURL() {
      const base = location.href.split("#")[0];
      const path = `#/share/true/${this._name}/${this._phone}/${this._email}/${this.tipoDeIngresoSeleccionado}/${this.tipoDeApoyoSeleccionado}/${this.montoDeApoyo}/${this.ingresoMensual}/${this.valorDelInmueble}/${this.montoDeCredito}/${this._montoDeCredito}/${this.tipoDePago}/${this.plazoSeleccionado}`;
      return encodeURIComponent(base + path);
    }
  },
  methods: {
    ...mapMutations([
      "updateModal",
      "updateName",
      "updateName",
      "updateEmail",
      "updatePhone"
    ]),
    ...mapActions(["addCreditShare"]),
    toAddCredit() {
      let data = {
        person: {
          name: this._name,
          email: this._email,
          phone: this._phone,
          status: 0,
          message: this.message,
          type: 0,
          password: "",
          date: new Date()
        },
        credit: this.activeBank,
        url: decodeURIComponent(this.buildURL)
      };
      this.addCreditShare(data);
    }
  }
};
</script>