<template>
  <div v-if="tipoDeIngresoSeleccionado == 'Asalariado'" class="center " >
    <br />Quieres utilizar:
    <br />
    <mu-radio value="cofinavit" v-model="tipoDeApoyo" label="Cofinavit" class="radio-apoyo"></mu-radio>
    <mu-radio value="infonavit" v-model="tipoDeApoyo" label="Apoyo Infonavit" class="radio-apoyo"></mu-radio>
    <mu-radio value="ninguno" v-model="tipoDeApoyo" label="Ningúno" class="radio-apoyo"></mu-radio>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapState([
      "tipoDeApoyoSeleccionado",
      "tipoDeIngresoSeleccionado"
    ]),
    tipoDeApoyo: {
      get() {
        return this.tipoDeApoyoSeleccionado;
      },
      set(value) {
        this.updateTipoDeApoyoSeleccionado(value);
      }
    }
  },
  methods: {
    ...mapMutations([
      "updateTipoDeApoyoSeleccionado"
    ])
  }
};
</script>ƒ