<template>
  <!-- <div class="card bank"> -->
  <div class="card bank" v-if="data.disponibilidad">
    <div class="title">
      <img :src="require('@/assets/img/bancos/logo-' + data.nombre + '.gif')" />
      <br />
    </div>
    <div class="data">
      <div>
        <span class="left-float">
          <b>Pago Mensual:</b>
        </span>
        <span class="right-float">
          <b>{{ data.pagoMensual | currency }}</b>
        </span>
      </div>
      <br />
      <div>
        <span class="left-float">Ingresos:</span>
        <span class="right-float">{{ data.ingresosRequeridos | currency }}</span>
      </div>
      <br />
      <div>
        <span class="left-float">Tasa Fija Anual:</span>
        <span class="right-float">{{(data.tasa * 100).toFixed(2)}} %</span>
      </div>
      <br />
      <div>
        <span class="left-float">Crédito Solicitado:</span>
        <span
          class="right-float"
        >{{(data.porcentajeDeCredito * 100).toFixed(2)}} % | {{ _montoDeCredito | currency }}</span>
      </div>

      <div v-if="creditoPreAutorizado">
        <br />
        <span class="left-float">Crédito Pre Autorizado:</span>
        <span class="right-float">{{ creditoPreAutorizado | currency }}</span>
        <br />
        <span class="info-bank">* Esta pre autorización no considera tus gastos en buró</span>
      </div>
      <pre v-if="debug" style="text-align:left;background:#333;color:white; display:block">
        <br />
        {{data}}
      </pre>
    </div>
    <br>
    <div class="buttons">
      <br />
      <div @click="modalInfo" class="button">INFORMES</div>
      <div @click="modalShare" class="button">COMPARTIR</div>
      <mu-tooltip content="Próximamente">
        <div class="button">TRAMITAR</div>
      </mu-tooltip>
      <br />
    </div>
  </div>
</template>
<script>
import { mapGetters, mapMutations, mapState } from "vuex";
import numeral from "numeral";

export default {
  props: ["data"],
  data() {
    return {
      debug: false
    };
  },
  computed: {
    ...mapGetters(["_montoDeCredito", "creditoPreAutorizado"]),
    ...mapState([
      "valorDelInmueble",
      "tipoDePago",
      "plazoSeleccionado",
      "montoDeApoyo",
      "tipoDeApoyoSeleccionado"
    ])
  },
  methods: {
    ...mapMutations(["updateModal", "updateActiveBank"]),
    format(value) {
      return numeral(value).format("0,0");
    },
    modalInfo() {
      this.updateModal(1);
      this.updateActiveBank({
        monthlyPayment: this.data.pagoMensual,
        earnings: this.data.ingresosRequeridos,
        mortgageRate: this.data.tasa,
        requestedCredit: this._montoDeCredito,
        preAuthorizedCredit: this.creditoPreAutorizado,
        propertyCost: this.valorDelInmueble,
        bank: this.data.nombre,
        paymentType: this.tipoDePago,
        lapse: this.plazoSeleccionado,
        support: this.montoDeApoyo,
        suportType: this.tipoDeApoyoSeleccionado
      });
    },
    modalShare() {
      this.updateModal(2);
      this.updateActiveBank({
        monthlyPayment: this.data.pagoMensual,
        earnings: this.data.ingresosRequeridos,
        mortgageRate: this.data.tasa,
        requestedCredit: this._montoDeCredito,
        preAuthorizedCredit: this.creditoPreAutorizado,
        propertyCost: this.valorDelInmueble,
        bank: this.data.nombre,
        paymentType: this.tipoDePago,
        lapse: this.plazoSeleccionado,
        support: this.montoDeApoyo,
        suportType: this.tipoDeApoyoSeleccionado
      });
    }
  }
};
</script>
