
<template>
  <div class="padding-internal-card">
    <mu-text-field v-model="_ingresoMensual" label="Tu ingreso Mensual" prefix="$" label-float></mu-text-field>
    <!-- <a href="https://api .whatsapp.com/send?phone=5650149322&text=123">hola</a> -->
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
import numeral from "numeral";

export default {
  computed: {
    ...mapState(["ingresoMensual"]),
    _ingresoMensual: {
      get() {
        return numeral(this.ingresoMensual).format("0,0");
      },
      set(value) {
        let valorNumerico = numeral(value).value();
        this.updateIngresoMensual(valorNumerico);
      }
    }
  },
  methods: {
    ...mapMutations(["updateIngresoMensual"])
  }
};
</script>
