<template>
  <div>
    <Header />
    <Body />
    <ShareModal />
    <InfoModal />
    <!-- <ShareAll /> -->
    <br />
  </div>
</template>
<script>
import Header from "./components/header/Header";
import Body from "./components/body/Body";
import ShareModal from "./components/modals/ShareModal";
import InfoModal from "./components/modals/InfoModal";
import ShareAll from "./components/modals/ShareAll";
import { mapMutations } from "vuex";
export default {
  components: {
    Header,
    Body,
    ShareModal,
    InfoModal,
    ShareAll
  },
  methods: {
    ...mapMutations([
      "updateName",
      "updateEmail",
      "updatePhone",
      "updateTipoDeIngresoSeleccionado",
      "updateTipoDeApoyoSeleccionado",
      "updateMontoDeApoyo",
      "updateIngresoMensual",
      "updateValorDelInmueble",
      "updateTipoDePago",
      "updatePlazoSeleccionado",
      "updateMontoDeCredito"
    ]),
    verifyURLShare() {
      var params = this.$route.params;
      console.log(params);

      if (params.share) {
        this.updateName(params.name);
        this.updateEmail(params.email);
        this.updatePhone(params.phone);
        this.updateTipoDeIngresoSeleccionado(params.tipoDeIngreso);
        this.updateTipoDeApoyoSeleccionado(params.tipoDeApoyo);
        this.updateMontoDeApoyo(parseFloat(params.montoDeApoyo));
        this.updateIngresoMensual(parseFloat(params.ingresoMensual));
        this.updateValorDelInmueble(parseFloat(params.valorDelInmueble));
        this.updateTipoDePago(params.tipoDePago);
        this.updatePlazoSeleccionado(params.plazo);
        this.updateMontoDeCredito(parseFloat(params.montoDeCredito));
      }
    }
  },
  mounted() {
    let data = {};
    this.verifyURLShare();
  }
};
</script>
