<template>
  <div v-if="valorDelInmueble">
    <div class="ui-container">
      <div @click="updateModal(3)" class="button-large">COMPARTIR TODO</div>
    </div>
    <div v-if="modal == 3" class="modal-cover">
      <div class="modal-content">
        <div class="card modal-card white inline-block">
          <!-- <h2 class="blue-text">Firma tu escritura en 3 o 4 semanas</h2> -->
          <img :src="require('@/assets/img/icons/share.svg')" class="modal-icon" />
          <div class="tabs">
            <div class="tab selected" style="padding-top:5px !important">
              <h3 style="color:#000 !important">COMPARTIR</h3>
            </div>
          </div>

          <mu-text-field v-model="_name" label="Nombre" label-float></mu-text-field>
          <mu-text-field v-model="_phone" :max-length="10" type="tel" label="Celular" label-float></mu-text-field>
          <mu-text-field v-model="_email" type="email" label="Correo Electrónico" label-float></mu-text-field>
          <mu-text-field v-model="message" label="Mensaje" label-float multi-line :rows="4"></mu-text-field>
          <br />
          <br />
          <div
            @click="toAddCredit"
            class="button-large"
            :class="{'disabled':disabled}"
          >Correo Electrónico</div>
          <div v-if="disabled" class="button-large" :class="{'disabled':disabled}">WhatsApp</div>
          <a
            :href="`https://api.whatsapp.com/send?phone=${contactData.phone}&text=${contactData.message}`"
            target="_blank"
            v-else
          >
            <div class="button-large" :class="{'disabled':disabled}">WhatsApp</div>
          </a>
          <div @click="updateModal(0)" class="button-large flat">Volver</div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
  data() {
    return {
      message: ""
    };
  },
  computed: {
    ...mapState([
      "modal",
      "activeBank",
      "name",
      "phone",
      "email",
      "tipoDeIngresoSeleccionado",
      "tipoDeApoyoSeleccionado",
      "montoDeApoyo",
      "ingresoMensual",
      "valorDelInmueble",
      "tipoDePago",
      "plazoSeleccionado",
      "montoDeCredito"
    ]),
    ...mapGetters(["_montoDeCredito"]),
    disabled() {
      if (this._name && this._phone && this._email && this.message) {
        return false;
      } else {
        return true;
      }
    },
    contactData() {
      return {
        phone: "52" + this._phone,
        message: this.buildURL
      };
    },
    buildURL() {
      const base = location.href.split("#")[0];
      const path = `#/share/true/${this._name}/${this._phone}/${this._email}/${this.tipoDeIngresoSeleccionado}/${this.tipoDeApoyoSeleccionado}/${this.montoDeApoyo}/${this.ingresoMensual}/${this.valorDelInmueble}/${this.montoDeCredito}/${this._montoDeCredito}/${this.tipoDePago}/${this.plazoSeleccionado}`;
      return base + path;
    },
    _name: {
      get() {
        return this.name;
      },
      set(value) {
        this.updateName(value);
      }
    },
    _email: {
      get() {
        return this.email;
      },
      set(value) {
        this.updateEmail(value);
      }
    },
    _phone: {
      get() {
        return this.phone;
      },
      set(value) {
        this.updatePhone(value);
      }
    }
  },
  methods: {
    ...mapMutations([
      "updateModal",
      "updateName",
      "updateName",
      "updateEmail",
      "updatePhone"
    ]),
    ...mapActions(["addCreditShare"]),
    toAddCredit() {
      let data = {
        person: {
          name: this._name,
          email: this._email,
          phone: this._phone,
          status: 0,
          message: this.message,
          type: 0,
          password: "",
          date: new Date()
        },
        credit: this.activeBank
      };
      this.addCreditShare(data);
    }
  }
};
</script>