<template>
  <div class="plazo-container">
    <div class="plazo" :class="{'selected':plazo == 2}" @click="plazo = 2">
      <span class="year">20</span>
      Años
    </div>
    <div class="plazo" :class="{'selected':plazo == 1}" @click="plazo = 1">
      <span class="year">15</span>
      Años
    </div>
    <div class="plazo" :class="{'selected':plazo == 0}" @click="plazo = 0">
      <span class="year">10</span>
      Años
    </div>
  </div>
</template>
<script>
import { mapMutations, mapState } from "vuex";
export default {
  computed: {
    ...mapState(["plazoSeleccionado"]),
    plazo: {
      get() {
        return this.plazoSeleccionado;
      },
      set(value) {
        this.updatePlazoSeleccionado(value);
      }
    }
  },
  methods: {
    ...mapMutations(["updatePlazoSeleccionado"])
  }
};
</script>

