
<template>
  <div class="padding-internal-card">
    <mu-text-field v-model="_valorDelInmueble" label="Valor del inmueble" prefix="$" label-float></mu-text-field>
    <!-- <a href="https://api .whatsapp.com/send?phone=5650149322&text=123">hola</a> -->
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
import numeral from "numeral";

export default {
  computed: {
    ...mapState(["valorDelInmueble"]),
    _valorDelInmueble: {
      get() {
        return numeral(this.valorDelInmueble).format("0,0");
      },
      set(value) {
        let valorNumerico = numeral(value).value();
        this.updateValorDelInmueble(valorNumerico);
      }
    }
  },
  methods: {
    ...mapMutations(["updateValorDelInmueble"])
  }
};
</script>
