<template>
  <div class="opciones-estaticas-container">
    <br />
    <div v-if="navbarFixed" class="navbar-fixed">
      <div class="container">
        <TipoDePago />
        <Plazo />
      </div>
    </div>
    <div :class="{'hide-opacity':navbarFixed}">
      <TipoDePago />
      <Plazo />
    </div>
  </div>
</template>
<script>
import TipoDePago from "./TipoDePago";
import Plazo from "./Plazo";
export default {
  components: { TipoDePago, Plazo },
  data() {
    return {
      scroll: 0
    };
  },
  computed: {
    navbarFixed() {
      if (this.scroll >= 290) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    scrollUpdate() {
      this.scroll = window.scrollY;
    }
  },
  mounted() {
    window.addEventListener("scroll", this.scrollUpdate, false);
  }
};
</script>
