<template>
  <div  class="padding-internal-card">
    <mu-select label="Tipo De Ingreso" placeholder="error input" v-model="tipoDeIngreso" full-width>
      <mu-option selected disabled value="0" label="Selecciona tu tipo de ingreso"></mu-option>
      <mu-option
        v-for="(option,index) in tiposDeIngreso"
        :key="index"
        :label="option"
        :value="option"
      ></mu-option>
    </mu-select>
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {};
  },
  computed: {
    ...mapState(["tiposDeIngreso", "tipoDeIngresoSeleccionado"]),
    tipoDeIngreso: {
      get() {
        return this.tipoDeIngresoSeleccionado;
      },
      set(value) {
        this.updateTipoDeIngresoSeleccionado(value);
      }
    }
  },
  methods: {
    ...mapMutations(["updateTipoDeIngresoSeleccionado"])
  }
};
</script>
