<template>
  <div v-if="valorDelInmueble">
    <Banco :key="index" v-for="(banco, index) in bancos" :data="banco" />
  </div>
</template>
<script>
import validator from "validator";

import Banco from "./components/Banco";

import { mapGetters, mapState } from "vuex";

export default {
  components: {
    Banco
  },
  computed: {
    ...mapGetters([
      "banamex",
      "scotiabank",
      "hsbc",
      "banorte",
      "santander",
      "banregio",
      "bxmas",
      "afirme"
    ]),
    ...mapState(["valorDelInmueble"]),
    bancos() {
      var bancos = [
        this.banamex,
        this.scotiabank,
        this.hsbc,
        this.banorte,
        this.santander,
        this.banregio,
        this.bxmas,
        this.afirme
      ];

      var sorted = bancos.sort((banco1, banco2) => {
        return banco1.pagoMensual - banco2.pagoMensual;
      });

      return sorted;
    }
  }
};
</script>
